<template>
  <div class="wyl_big">
    <!-- header  面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- header end -->
    <!-- 卡片试图  start -->
    <el-card>
      <!-- 添加角色 -->
      <el-row>
        <el-col>
          <el-button type="primary">添加角色</el-button>
        </el-col>
      </el-row>

      <!-- table  start-->
      <el-table :data="rolesList" border stripe>
        <!-- 展开列 -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['bdbottom',m.id==0?'bdtop':'','vcenter']"
              v-for="m in scope.row.children"
              :key="m.id"
            >
              <!-- 一级权限 -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row,m.id)">{{m.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 二三级权限 -->
              <el-col :span="19">
                <el-row
                  :class="[i1==0?'':'bdtop','vcenter']"
                  v-for="(m1,i1) in m.children"
                  :key="m1.id"
                >
                  <!-- 二级 -->
                  <el-col :span="6">
                    <el-tag
                      closable
                      @close="removeRightById(scope.row,m1.id)"
                      type="success"
                    >{{m1.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!-- 三级 -->
                  <el-col :span="18">
                    <el-tag
                      closable
                      @close="removeRightById(scope.row,m2.id)"
                      type="warning"
                      v-for="m2 in m1.children"
                      :key="m2.id"
                    >{{m2.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!-- 序号列 -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="roleName" label="角色名称"></el-table-column>
        <el-table-column prop="roleDesc" label="角色描述"></el-table-column>
        <el-table-column label="操作" width="350">
          <template slot-scope="scope">
            <el-button
              @click="showEditDialog(scope.row)"
              size="mini"
              type="primary"
              icon="el-icon-edit"
            >编辑</el-button>
            <el-button
              @click="removeUserById(scope.row.id)"
              size="mini"
              type="danger"
              icon="el-icon-delete"
            >删除</el-button>
            <el-button
              @click="ShowSetRightDialog(scope.row)"
              size="mini"
              type="warning"
              icon="el-icon-setting"
            >分配权限</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- table   end -->
    </el-card>
    <!-- 卡片试图  end -->

    <!-- 分配权限的模态框  start  -->
    <el-dialog
      title="分配权限"
      @close="setRightDialogClose"
      :visible.sync="setRightDialogVisible"
      width="50%"
    >
      <!-- 树形控件 -->
      <el-tree
        show-checkbox
        default-expand-all
        :default-checked-keys="defkeys"
        ref="treeRef"
        node-key="id"
        :data="rightList"
        :props="treeProps"
      ></el-tree>

      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allotrights">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 分配权限的模态框  end  -->
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      // 角色数据接口的列表
      rolesList: [],
      //   分配权限的模态框  显示隐藏
      setRightDialogVisible: false,
      //   所有数据权限的数据
      rightList: [],
      // 树形控件的绑定属性
      treeProps: {
        label: "authName",
        children: "children"
      },
      //   树形控件  默认展开的节点的 key 的数组
      defkeys: [],
      //   当前即将分配权限的id
      rowId: ""
    };
  },
  props: {},
  components: {},
  mounted() {
    //   获取所有角色的列表
    this.getRolesList();
  },
  methods: {
    //   获取所有角色的列表
    async getRolesList() {
      let { data: res } = await this.$http.get(`roles`);
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
      this.rolesList = res.data;
      console.log(this.rolesList);
    },
    // 删除 页面   提示消息
    async removeRightById(row, rightId) {
      console.log(row.id + rightId);
      console.log(row.id);
      // console.log(id)
      let con = await this.$confirm(
        "此操作将永久删除该文件, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      ).catch(err => err);

      if (con !== "confirm") {
        return this.$message.info("已取消删除");
      }

      // 删除 权限id
      let { data: res } = await this.$http.delete(
        `roles/${row.id}/rights/${rightId}`
      );
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }

      this.$message.success(res.meta.msg);
      //   this.getRolesList();
      row.children = res.data;
    },
    // 分配权限的模态框  点击事件
    async ShowSetRightDialog(row) {
      this.rowId = row.id;
      // 通过递归的函数 获取角色下列三级权限节点得id
      this.getLeafKeys(row, this.defkeys);
      // 模态框的显示隐藏
      this.setRightDialogVisible = true;
      let { data: res } = await this.$http.get(`rights/tree`);
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
      this.$message.success(res.meta.msg);
      this.rightList = res.data;
    },
    // 通过递归的函数 获取角色下列三级权限节点得id，并保存到defkeys数组中啊
    getLeafKeys(node, arr) {
      console.log(node.id + 12341234567123456);
      if (!node.children) {
        return arr.push(node.id);
      }
      node.children.forEach(item => {
        this.getLeafKeys(item, arr);
      });
    },
    //监听分喷权限对话框的关闭事件
    setRightDialogClose() {
      this.defkeys = [];
    },
    // 点击为角色分配权限
    async allotrights() {
      let keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ];
      console.log(keys);
      let inStr = keys.join(",");
      let { data: res } = await this.$http.post(`roles/${this.rowId}/rights`, {
        rids: inStr
      });
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
      this.$message.success(res.meta.msg);
      this.getRolesList();
      this.setRightDialogVisible = false;
    }
  }
};
</script>

<style scoped lang='scss'>
.el-tag {
  margin: 0.07rem;
}
.bdtop {
  border-top: 1px solid rgb(236, 230, 230);
}
.bdbottom {
  border-bottom: 1px solid #eee;
}

.vcenter {
  display: flex;
  align-items: center;
}
</style>